.shopping-cart {
  background-image: url("/icon/shopping-cart.svg");
}

.book {
  background-image: url("/icon/book.svg");
}

.phone {
  background-image: url("/icon/mobile.svg");
}

.active > .shopping-cart {
  background-image: url("/icon/shopping-cart-fill.svg");
}

.active > .book {
  background-image: url("/icon/book-fill.svg");
}

.active > .phone {
  background-image: url("/icon/mobile-fill.svg");
}
