.goods-info-wrapper{
  height:100%;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.goods-info{
  font-size: 75px;
  height: 75px;
  max-width: 160px;
  margin-bottom: 25px;
  border: 1px solid;
}

.control-panel{
  font-size: 25px;
  display: flex;
  justify-content: space-between;
  width: 100px;
}

.control-panel>div {
  height:25px;
  width: 25px;
  border: 1px solid;
  display: flex;
  justify-content: center;
  align-items: center;
}
