ul {
  list-style: none;
}

.list-wrapper {
  flex-grow: 7;
  border-top: 1px solid;
}

.list-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.item-info {
  display: flex;
  align-items: center;
}

.item-info > span:first-child {
  width: 40px;
  height: 40px;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

